//‰ª•‰∏ä‰ª£Á†ÅÊòØÊîæÂú®divÈáåÈù¢ÁöÑÔºå‰∏á‰∏ÄÊúâ‰∫õÂÖÉÁ¥†‰∏çËÉΩÊîæÂú®divÈáåÈù¢Âë¢ÔºåÊúâÊ≤°Êúâ‰∏áËÉΩÁöÑÂÜôÊ≥ï---template
dom.create = function(string){
    const container = document.createElement("template")
    container.innerHTML = string.trim()//trim()ÊòØ‰∏∫‰∫ÜÈô§ÂéªstringÈáåÁöÑÁ©∫Ê†º‰ª•ÂèäÂõûËΩ¶
    return container.content.firstChild

}
//‰ª•‰∏ã‰ª£Á†Å‰∏∫Êñ∞Â¢ûÂºüÂºü
dom.after = function(node,node2){ //Êé•Âèónode2‰Ωú‰∏∫nodeËäÇÁÇπÁöÑÂºüÂºüËäÇÁÇπ
    node.parentNode.insertBefore(node2,node.nextSibling)//ÊâæÂà∞ËäÇÁÇπÁöÑÁà∏Áà∏ÔºåË∞ÉÁî®insertBeforeÊäänode2ÊèíÂà∞node‰∏ã‰∏Ä‰∏™ËäÇÁÇπÁöÑÂâçÈù¢
}

//‰ª•‰∏ã‰ª£Á†Å‰∏∫Êñ∞Â¢ûÂì•Âì•
dom.before = function(node,node2){
    node.parentNode.insertBefore(node2,node)//ÊäänodeÊèíÂà∞nodeÁöÑÂâçÈù¢
}

//‰ª•‰∏ã‰ª£Á†ÅÁî®‰∫éÊñ∞Â¢ûÂÑøÂ≠ê
dom.append = function(parent,node){
    parent.appendChild(node)
}

//‰ª•‰∏ã‰ª£Á†ÅÁî®‰∫éÊñ∞Â¢ûÁà∏Áà∏
dom.wrap = function(node,parent){//ÊääÁà∏Áà∏Âä†Âà∞ËäÇÁÇπÂ§ñÈù¢Â∞±ÊòØÂä†Áà∏Áà∏
    dom.before(node,parent)//Êñ∞Â¢ûparentÂõ¥ÊÆ¥nodeÁöÑÂì•Âì•Ôºå‰πüÂ∞±ÊòØÊîæÂú®nodeÁöÑÂâçÈù¢ÔºåÂ∞±ÊòØÂÖàÊääÊéíÂ∫èÂÖàÊéíÂ∫è
    dom.append(parent,node)//ÁÑ∂ÂêéÂú®ÊîπÂèòÂ±ÇÁ∫ßÔºåÊäänode‰Ωú‰∏∫parentÁöÑÂÑøÂ≠ê„ÄÇappendÊúâ‰∏™ÁâπÊÄßÂ∞±ÊòØ‰Ω†ÊääÂÖÉÂÖÉÁ¥†ÊèíÂà∞Âà´ÁöÑÂú∞ÊñπÔºå‰ªñ‰ºöÊääÂÖÉÁ¥†Âú®ÂéüÊù•ÁöÑÂú∞ÊñπÁßªÂºÄ
}

//‰ª•‰∏ã‰ª£Á†Å‰∏∫Âà†Èô§ËäÇÁÇπ
dom.remove = function(node){
    node.parentNode.removeChild(node)
    return node//ÊääÂÆÉËøîÂõûÂá∫Êù•ÔºåËØ¥‰∏çÂÆö‰ª•ÂêéËøò‰ºöÁî®Âà∞Ëøô‰∏™Â∑≤ÁªèÂà†Èô§ÁöÑËäÇÁÇπÂë¢
}

//‰ª•‰∏ã‰ª£Á†Å‰∏∫Âà†Èô§ÊâÄÊúâÂÑøÂ≠êÔºàÂà†Èô§Âêé‰ª£Ôºâ
dom.empty = function(){
    //node.innerHTML = ''//ËøôÊòØÊúÄÂø´ÊúÄÁÆÄÂçïÁöÑÊñπÊ≥ïÔºå‰ΩÜÊòØÊúâÊó∂ÂÄôÈúÄË¶ÅËøîÂõûËøô‰∏™Ë¢´Âà†Èô§ÁöÑÊï∞ÁªÑÔºåÊâÄ‰ª•ÂèØ‰ª•Â∞ùËØï‰ª•‰∏ãÂÖ∂‰ªñÊñπÊ≥ï
    const {childNodes} = node //Á≠â‰ª∑‰∫éconst childNodes = node.childNodes
    const array = []//ÂàõÂª∫‰∏Ä‰∏™Á©∫Êï∞ÁªÑÊù•Ë£ÖË¢´Âà†Èô§ÁöÑÂÑøÂ≠êÔºå‰∏á‰∏Ä‰ª•ÂêéÁî®Âà∞Âë¢
    let x = node.firstChild
    while(x){
        array.push(dom.remove(node.firstChild))
        x = node.firstChild//Âõ†‰∏∫ÊØèÂà†Èô§‰∏Ä‰∏™Ôºå‰∏ã‰∏Ä‰∏™ËäÇÁÇπÂ∞±ÂèòÊàê‰∫ÜÁ¨¨‰∏Ä‰∏™Â≠©Â≠êÔºåÂæóÈáçÊñ∞ËµãÂÄºÁªôx„ÄÇËÄå‰∏înode.childÁöÑÈïøÂ∫¶ÊòØ‰ºöÂèòÁöÑÔºåÊâÄ‰ª•‰∏çËÉΩÁî®forÂæ™ÁéØ
    }
    return array
}

//‰ª•‰∏ã‰ª£Á†Å‰∏∫‰øÆÊîπÂíåËØªÂ±ûÊÄß
dom.attr = function(node,name,value){
    if(arguments.length === 3){
        node.setAttribute(name,value)//Â¶ÇÊûúÊé•Âèó‰∫Ü3‰∏™ÂèÇÊï∞ÔºåËØ¥ÊòéÊòØ‰øÆÊîπÂ±ûÊÄß
    }else if(arguments.length === 2){
        node.getAttribute(name)//Â¶ÇÊûúÊé•Âèó‰∏§‰∏™ÂèÇÊï∞ÂàôËØ¥ÊòéË¶ÅËØªÂèñÂ±ûÊÄß
    }
}

//‰ª•‰∏ã‰ª£Á†Å‰∏∫‰øÆÊîπÊñáÊú¨ÂíåËØªÂèñÊñáÊú¨
dom.text = function(node,string){
    if(arguments.length === 2){//ÈáçËΩΩÔºåÁúãÊòØ‰º†Âá†‰∏™ÂèÇÊï∞Ëøá‰∫ÜÔºåÂà§Êñ≠ÊòØ‰øÆÊîπËøòÊòØËØªÂèñ
        if('innerText' in node){//ÈÄÇÈÖçÊµèËßàÂô®
            node.innerText = string//ËøôÊòØieÁöÑÂÜôÊ≥ï
        }else{
            node.textContent = string//ËøôÊòØchromeÁöÑÂÜôÊ≥ï
        }
    }else{
        if('innerText' in node){//ÈÄÇÈÖçÊµèËßàÂô®
           return node.innerText //ËøôÊòØieÁöÑÂÜôÊ≥ï
        }else{
           return node.textContent //ËøôÊòØchromeÁöÑÂÜôÊ≥ï
        }
    }
}

//‰ª•‰∏ã‰ª£Á†Å‰∏∫ËØªÂèñÂíå‰øÆÊîπhtmlÂÜÖÂÆπ
dom.html = function(node,string){
    if(arguments.length === 2){
        node.innerHTML = string
    }else if(arguments.length ===1){
        return node.innerHTML

    }
}

//‰ª•‰∏ã‰ª£Á†Å‰∏∫‰øÆÊîπstyle
dom.style = function(node,name,value){
    if(arguments.length === 3){
        //‰æãÂ¶Çdom.style(div,'color','red'),ÂæàÊòéÊòæÊÑèÂõæÊòØÊää‰øÆÊîπÂ±ûÊÄß
        node.style[name] = value
    }else if(arguments.length === 2){
        if(typeof name === 'string'){//ËØªÂèñÂ±ûÊÄßÂêç
            return node.style[name]
        }else if(name instanceof Object){//Â¶ÇÊûúÊòØobjectÁöÑÂÆû‰æã,Ê∑ªÂä†‰øÆÊîπstyle
            for(let key in name){
                node.style[key] = name[key]
            }
        }
    }
}

//‰ª•‰∏ã‰ª£Á†Å‰∏∫Ê∑ªÂä†class
dom.class.add = function(node,className){
    node.classList.add(className)
}
//‰ª•‰∏ã‰ª£Á†Å‰∏∫Âà†Èô§class
dom.class.remove = function(node,className){
    node.classList.remove(className)
}
//‰ª•‰∏ã‰∏∫ÁúãclassÊúâÊ≤°Êúâ
dom.class.has = function(node,className){
    return node.classList.contains(className)
}

//‰ª•‰∏ã‰∏∫Ê∑ªÂä†‰∫ã‰ª∂ÁõëÂê¨
dom.on = function(node,eventName,fn){
    node.addEventListener(eventName,fn)
}
//‰ª•‰∏ã‰ª£Á†Å‰∏∫Âà†Èô§‰∫ã‰ª∂ÁõëÂê¨
dom.off = function(node,eventName,fn){
    node.removeEventListener(eventName,fn)
}

//Êü•ÔºåËé∑ÂèñÊ†áÁ≠æÊàñËÄÖÊ†áÁ≠æ‰ª¨
dom.find = function(selector){
    return document.querySelectorAll(selector)
}
/* //Â¶ÇÊûúÊàëÊÉ≥Ë¶ÅÊâæÁâπÂÆöÁöÑ‰∏Ä‰∏™Ê†áÁ≠æÂë¢ÔºåÂæóÈôêÂÆö‰∏Ä‰∏™Êü•ÊâæËåÉÂõ¥
dom.find = function(selector,scope){//Êé•Âèó‰∏Ä‰∏™ÈÄâÊã©Âô®ÔºåËåÉÂõ¥
    return (scope || document).querySelectorAll(selector)//Â¶ÇÊûúÊúâscopeÂ∞±Âú®scopeË∞ÉÁî®ÔºåÊ≤°ÊúâÂ∞±Áî®document
} */

//Ëé∑ÂèñÁà∂ÂÖÉÁ¥†
dom.parent = function(node){
    return node.parentNode
}

//Ëé∑ÂèñÂ≠êÂÖÉÁ¥†
dom.children = function(node){
    return node.children
}

//Ëé∑ÂèñÂÖÑÂºüÂßêÂ¶π
dom.siblings = function(node){
    return Array.from(node.parentNode.children).filter(n => n !== node)//ÊâæÂá∫ËäÇÁÇπÁà∏Áà∏ÁöÑÂ≠©Â≠êÔºåÂæóÂà∞‰∏Ä‰∏™‰º™Êï∞ÁªÑÔºåÁÑ∂ÂêéÂèòÊàêÊï∞ÁªÑÔºåÂú®ÊéíÈô§ËäÇÁÇπËá™Â∑±ÔºåÂæóÂà∞ÂÖÑÂºüÂßêÂ¶πÁöÑÊï∞ÁªÑÔºåÁÑ∂ÂêéËøîÂõûÂ∞±ÂèØ‰ª•‰∫Ü
}

//Ëé∑ÂèñÂºüÂºü
dom.next = function(node){
    let x = node.nextSibling
    while(x && x.nodeType ===  3){//Âõ†‰∏∫ÊúâÂèØËÉΩ‰∏ã‰∏Ä‰∏™ËäÇÁÇπÊòØÊñáÊú¨ËäÇÁÇπÔºå‰æãÂ¶ÇÁ©∫Ê†ºÂõûËΩ¶‰ªÄ‰πàÁöÑÔºåÊâÄ‰ª•Ë¶ÅÂà§Êñ≠ÂÆÉÁöÑËäÇÁÇπÁ±ªÂûãÊòØ‰∏çÊòØÊñáÊú¨„ÄÇ&&ÊòØÂà§Êñ≠xÂ≠ò‰∏çÂ≠òÂú®ÁöÑ
        x = node.nextSibling
    }
    return x
}

//Ëé∑ÂèñÂì•Âì•
dom.previous = function(node){
    let x = node.previousSibling
    while(x && x.nodeType === 3){
        x = node.previousSibling
    }
    return x
}

//ÈÅçÂéÜÊâÄÊúâËäÇÁÇπ
dom.each = function(nodeList,fn) {
    for(let i = 0;i < nodeList.length;i++){
        fn.call(null,nodeList[i])
    }
}

//ÊéíË°åËÄÅÂá†
dom.index = function(node){
    const list = dom.children(node.parent)//ÊâæËäÇÁÇπÁà∏Áà∏ÁöÑÂÑøÂ≠ê
    let i 
    for(i = 0;i < list.length;i++){
        if(list[i] === node){
            break
        }
    }
    return i//ËøîÂõûÁöÑÊòØ‰∏ãÊ†á
}
